export const telegramForm = ({price, dataCheckout, basketProducts}) => {

  const renderOrders = (products) => {
    let text = '';
    products.forEach((item, index) => {
      text = text +
        `--------------------------------------------------\n` +
        `<i>:</i> ${item.name}\n` +
        `<i> ():</i> ${item.price}Br\n` +
        `<i> ():</i> ${item.quantity}\n`
    });
    return text;
  };

  const MESSAGE =
    `<b> :</b>\n\n` +
    `${renderOrders(basketProducts)}--------------------------------------------------\n\n--------------------------------------------------\n` +
    `<i> :</i> ${basketProducts.length}(.)\n` +
    `<i> :</i> ${dataCheckout.paymentType}\n` +
    `<i> :</i> ${dataCheckout.deliveryType}\n` +
    `<i> :</i> ${price.deliveryPrice}Br\n` +
    `<i>:</i> ${price.basketPrice}Br\n\n` +
    `<b>:</b> ${price.generalPrice}Br\n--------------------------------------------------\n\n` +
    `--------------------------------------------------\n` +
    `<i>:</i> ${dataCheckout?.name}\n` +
    `<i>:</i> ${dataCheckout.region} , .${dataCheckout.citi}, ${dataCheckout.address}\n` +
    `<i>Telegram:</i> @${dataCheckout?.telegram}\n` +
    `<i>Email:</i> ${dataCheckout?.email}\n` +
    `<i>:</i> ${dataCheckout?.photo}\n` +
    `--------------------------------------------------\n\n`;

  return MESSAGE;
}