import styles from './AuthResetPassword.module.css';
import {Form, Formik} from "formik";
import {InputText} from "../AuthForms/InputValidate/InputText/InputText";
import icon_back from "../../../image/arrowMenuMobil.png";
import {NavLink} from "react-router-dom";
import icon_logo from "../../../image/logo.png";

export const AuthResetPassword = () => {

  const submitSingIn = (values) => {
    values.captcha = '';
  };

  let buttonAuthDisable = false;

  return (
    <Formik
      initialValues={{
        email: '',
        password: '',
        rememberMe: false,
      }}
      onSubmit={submitSingIn}
    >
      {({
          errors,
          touched,
      }) => {
        return (
          <div className={styles.wrapper}>
            <NavLink to='/login' className={styles.backPage}>
              <img src={icon_back} alt="icon_back"/>
              
            </NavLink>
            <img src={icon_logo} className={styles.logo}/>
            <div className={styles.block}>
              <Form className={styles.formBlock}>
                <p className={styles.name}> :</p>
                <InputText
                  errors={errors.email}
                  touched={touched.email}
                  name='email'
                  type='email'
                  placeholder=' Email...'
                />
                <button
                  className={'gold'}
                  type="submit"
                  disabled={buttonAuthDisable}
                > </button>
              </Form>
            </div>
          </div>
        )
      }}
    </Formik>
  );
};
